@value textLightColor, textMiddleColor, backgroundLightColor, backgroundMiddleColor, borderMiddleColor, shadowMiddle from "utilities/variables.module.css";

.transactionHistory {
  margin: 0 auto;
  width: 60%;
  min-width: 500px;

  overflow: hidden;

  color: textMiddleColor;
  background-color: backgroundLightColor;
  border-radius: 5px;
  border: none;
  border-spacing: 0;
  border-collapse: collapse;
  box-shadow: shadowMiddle;
}

thead {
  background-color: rgb(1, 180, 204);

  color: textLightColor;

  font-size: 16px;
  text-transform: uppercase;
}

th {
  width: calc(100% / 3);
  padding: 15px 5px;
}

th:not(:last-child) {
  border-right: 1px solid backgroundLightColor;
}

td {
  padding: 15px 5px;
  font-size: 16px;
  text-align: center;
}

td:not(:last-child) {
  border-right: 1px solid borderMiddleColor;
}

tr:nth-child(2n) {
  background-color: backgroundMiddleColor;
}
