@value textMiddleColor, backgroundLightColor, backgroundMiddleColor, borderMiddleColor, shadowMiddle from "utilities/variables.module.css";

.profile {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 350px;

  overflow: hidden;

  background-color: backgroundLightColor;
  border-radius: 5px;
  box-shadow: shadowMiddle;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar {
  display: block;
  margin: 40px auto;
  width: 150px;
  height: 150px;
  object-fit: cover;

  border-radius: 50%;
}

.name {
  display: block;
  margin: 0;
  margin-bottom: 15px;
  font-size: 30px;
  font-weight: 500;
  text-align: center;
}

.tag {
  composes: name;

  color: textMiddleColor;
  font-size: 20px;
  font-weight: 400;
}

.location {
  composes: tag;

  margin-bottom: 40px;
}

.stats {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100%;

  list-style: none;

  background-color: backgroundMiddleColor;
  border-top: 1px solid borderMiddleColor;
}

.stats > li {
  flex-basis: calc(100% / 3);
  padding: 10px;
}

.stats > li:not(:last-child) {
  border-right: 1px solid borderMiddleColor;
}

.label {
  composes: tag;
  display: block;
  margin-bottom: 10px;

  font-size: 18px;
}

.quantity {
  composes: name;
  margin-bottom: 0;
  font-size: 25px;
}
